@import "../../global.scss";
.contacts{
    background-color: $mainColor;
    display: flex;
    color: white;
    @include mobile{
        flex-direction:column-reverse;
    }
    .left{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        flex: 1;
        margin-left: 10px;
        @include mobile{
            flex: auto;
            height: 160px;
            margin-left: 0px;
        }
        .emailHolder{
            display: flex;
            width: 500px;
            height: 500px;
            border-radius: 50%;
            background-color: black;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            @include mobile{
                justify-content: flex-start;
                width: 100vw;
                height: inherit;
                border-radius: 0px;
            }
            h1{
                font-size: 50px;
                @include mobile{
                    margin-top: 30px;
                    font-size: 25px;
                }
            }
            h2{
                font-size: 40px;
                color: $orange;
                @include mobile{
                    font-size: 28px;
                }
            }
            .linkHolder{
                align-items: center;
                justify-content: center;
                display: none;
                flex-direction: row;
                overflow: hidden;
                margin-top: 10px;
                @include mobile{
                    margin-top: 10px;
                    display: flex;
                }
                .link1{
                    font-size: 16px;
                    font-weight: 500;
                    justify-content: center;
                    align-items: center;
                    .gitLogo{
                        font-size: 40px;
                        color: $secondColor2;
                        margin-bottom: 2px;
                        transition: all 1s ease;
                    }
                    @include mobile{
                        display: flex;
                    }
                }
                .link2{
                    font-size: 16px;
                    font-weight: 500;
                    justify-content: center;
                    align-items: center;
                    margin-left: 20px;
                    .linkedinLogo{
                        font-size: 40px;
                        color: $secondColor2;
                        margin-bottom: 2px;
                        transition: all 1s ease;
                        
                    }
                    @include mobile{
                        display: flex;
                    }
                }
            }
        }
        
    }
    .right{
        display: flex;
        flex: 2;
        @include mobile{
            height: calc(100vh - 240px);
            flex: auto;
        }
        .preResponse{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: inherit;
            display: flex;
            &.active{
                display: none;
            }
            .rightHeader{
                display: flex;
                font-size: 30px;
                @include mobile{
                    font-size: 20px;
                }
            }
            .contactForm{
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                margin-top: 20px;
                input{
                    padding: 10px 10px;
                    border-radius: 10px;
                    width: 800px;
                    height: 25px;
                    font-size: 23px;
                    display: flex;
                    @include mobile{
                        width: 80vw;
                        font-size: 18px;
                        height: 20px;
                    }
                }
                textarea{
                    display: flex;
                    padding: 10px 10px;
                    margin-top: 20px;
                    border-radius: 10px;
                    width: 800px;
                    height: 250px;
                    font-size: 23px;
                    @include mobile{
                        margin-top: 20px;
                        width: 80vw;
                        font-size: 18px;
                        height: 180px;
                    }
                }
                button{
                    cursor: pointer;
                    margin-top: 20px;
                    color: $orange;
                    background-color: black;
                    width: 300px;
                    height: 50px;
                    border: none;
                    border-radius: 10px;
                    font-size: 20px;
                    @include mobile{
                        font-size: 20px;
                        width: 100px;
                        height: 50px;
                    }
                }
            }
        }
        
        .postResponse{
            display: flex;
            flex-direction: column;
            margin-top: 40px;
            display: none;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            width: 100%;
            height: inherit;
            font-size: 35px;
            font-weight: 700;
            color: $orange;
            overflow: hidden;
            &.active{
                display: flex;
            }
        }
    }
}